@charset "utf-8";

@font-face{
    font-family:KBODia;
    src:url("../public/font/KBO Dia Gothic_medium.otf");
    src:url("../public/font/KBO Dia Gothic_medium.otf")format("embedded-opentype"),
            url("../public/font/KBO Dia Gothic_medium.ttf")format("truetype"),
            url("../public/font/KBO-Dia-Gothic_medium.woff")format("woff");
}





/*reset*/
body,div,header,h1,a,img,ul,li,nav,footer,address,p,br,span,strong,h2,h3,article,h4{margin:0; padding:0;}
header,nav,footer,article{display:block;}
ul,li{list-style:none;}
img{border:none;}
a{text-decoration:none; color:#333333;}
body,h1,h2,h3,h4,div,li,p{font:normal 14px/1.5em "KBODia",sans-serif; color:#333333;}







