#wrap{width: 100%; height: 100%; margin: 0 auto; min-width: 320px;}
.top {
    width: 80%;
    height: 70px;
    margin: 20px auto 10px; 
}

.top h2 {
    font-size: 20px;
    font-weight: bold;
}

.txt_wrap{
    width: 80%;
    height: 400px;
    margin: 0 auto;
}

.txt{
    width: 100%;
    margin-bottom: 20px;
}
.txt h3{
    font-weight: bold;
    font-size: 16px;
    text-indent: 10px;
    margin-bottom: 10px;
}

.txt p, .txt ul li{margin-left: 10px; margin-bottom: 10px;}
.txt ul li span{display: block; margin-left: 20px; text-indent: -15px;}  
