#main_wrap {
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

.main {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
    margin: 0 auto;
}

.con1 {
    width: 80%;
    height: 90px;
    margin: 20px auto;
}

.con1 h2 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
}

.con1 p {
    font-size: 15px;
    font-weight: bold;
    text-align: center;
}

.game_wrap {
    margin: 0 auto;
    clear: both;
    text-align: center;
    width: 70%;
    height: 400px;
    /* position: absolute;
    left: 50%;
    bottom: 50px; */
    /* transform: translate(-50%, 0); */
}

.game {
    width: 100%;
    height: 100%;
}

.game li {
    width: 100%;
    height: 20%;
    border-bottom: 1px solid #999;
    box-sizing: border-box;
    position: relative;
}
.game li:last-child{border-bottom: none;}

.game li p:nth-of-type(1) {
    float: left;
    width: 20%;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
}

.game li p:nth-of-type(2) {
    float: left;
    width: 45%;
    position: absolute;
    left: 25%;
    top: 50%;
    transform: translate(0, -50%);
}

.game li a {
    float: left;
    width: 75px;
    height: 30px;
    display: block;
    background-color: #1b2a7b;
    color: #fff;
    margin-left: 20px;
    line-height: 30px;
    position: absolute;
    left: 70%;
    top: 50%;
    transform: translate(0, -50%);
    transition: all 0.5s ease;
    box-sizing: border-box;
}


.game li a:hover {
    box-sizing: border-box;
    border: 2px solid #1b2a7b;
    background-color: #fff;
    color: #1b2a7b;
}


.icon_wrap{width: 100%; height: 80px; background-color: #ddd; margin-bottom: 50px;}
.con_icon{
    width: 80%;
    height: 80px;
    margin: 30px auto;
}
.con_icon li a{
    padding-top: 20px;
    float: left;
    width: 20%;
height:80px;
text-align: center;
}
.con_icon li:nth-child(1) a{background: url(../../public/images/score.png) no-repeat center 40px;}
.con_icon li:nth-child(2) a{background: url(../../public/images/people.png) no-repeat center 40px;}
.con_icon li:nth-child(3) a{background: url(../../public/images/highlight.png) no-repeat center 40px;}
.con_icon li:nth-child(4) a{background: url(../../public/images/weather.png) no-repeat center 40px;}
.con_icon li:nth-child(5) a{background: url(../../public/images/store.png) no-repeat center 40px;}


/* insta_wrap */
.insta_wrap{
    width: 80%;
    height: 250px;
    margin: 0 auto 20px;
    min-width: 320px;

}

.insta_wrap h2{
    width: 95%;
    margin: 0 auto 20px;
}
.insta_wrap h2 span{
    float: right;
    width: 25px;
    height: 25px;
    line-height: 25px;
    display: block;
    text-align: center;
    font-weight: normal;
    border: 1px solid #000;
}

.insta_con{
    clear: both;
    width: 100%;
    height: 150px;
}
.insta_con li {
    float: left;
    width: 45%;
    height: 180px;

    position: relative;
    margin: 0 2%;
    overflow: hidden;
}
.insta_con li:first-child{
    background: #120d0d url(../../public/images/img_insta1.png) no-repeat center 80px;
    background-size: 100%;
}
.insta_con li:last-child{
    background: #292122 url(../../public/images/img_insta2.png) no-repeat center 80px ;
    background-size: 100%;
}
.insta_con li a{
    position: absolute;
    width: 100%;
    top: 50px;
    left: 50%;
    top: 30%;
    transform: translate(-50%,-50%);
    color: #fff;
    text-align: center;
   
}
.insta_con li:first-child a span{
    background-color: blue;
}
.insta_con li:last-child a span{
    background-color: red;
}


