#wrap {
    width: 100%;
    height:100%; margin: 0 auto;
    min-width: 320px;
}

.top {
    width: 80%;
    height: 70px;
    margin: 20px auto 10px;
}

.top h2 {
    font-size: 20px;
    font-weight: bold;
}


/* news_wrap */
.news_wrap {
    width: 80%;
    height: 100%;
    margin: 0 auto;
}

.news {
    width: 300px;
    height: 1000px;
    margin: 0 auto;
}

/* news1 */
.news1 {
    width: 100%;
    height:150px;
    border-bottom: 1px solid #999;
    margin-bottom: 20px;
}

.news1 h3 {
    float: left;
    width: 100%;
    height: 30px;
}
.news1 h3 span{
    float: right;
    width: 28%;
    height: 30px;
}


.news_sec {
    width: 100%;
    float: left;
    height: 110px;
    /* border: 1px solid green; */
    overflow: hidden;
}

.news_txt1 {
    overflow: hidden;
    width: 100%;
    height: 30px;
    /* border: 1px solid red; */
    text-align: left;
    font-size: 15px;
    font-weight: bold;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.news_txt2 {
    width: 100%;
    height: 100px;
    border: 1px solid vil;
    text-align: left;
    font-size: 13px;
    overflow: hidden;
    white-space: normal;
    text-overflow: ellipsis;
}