 h2 {
    font-size: 20px;
    font-weight: bold;
}

#wrap {
    width: 100%;
    height: 850px;
    margin: 0 auto;
}

.top {
    width: 80%;
    height: 70px;
    margin: 20px auto 10px;
}



.contents {
    width: 300px;
    height: 1000px;
    margin: 0 auto;
}

.con li {
    width: 100%;
    height: 33.3%;
    margin: 10px auto;
    margin-bottom: 40px;
    border-bottom: 1px solid #999;
    padding-bottom: 20px;
}

.con li video {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
} 


