#bottom_wrap {
    width: 100%;
    height: 120px;
    margin: 0 auto;
    background-color: #D9D9D9;
}

.bottom {
    width: 100%;
    height: 120px;
    position: relative;
    margin: 0 auto;
}

.btn_wrap {
    width: 300px;
    height: 40px;
    ;
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.btn_wrap button {
    margin: 0 10px;
    width: 120px;
    height: 40px;
    outline: none;
    background-color: #fff;
    border: 1px solid grey;
    cursor: pointer;
}

.bottom p:nth-child(2) {position: absolute; bottom: 10px; left: 50%; transform: translate(-50%,0);}