html,body{height: 100%;}
input{outline:none;}
body,input{-webkit-user-select: none; -moz-user-select: none;-ms-user-select: none;user-select: none;  font:normal 15px/1.5em Arial,sans-serif; color:#333333;}

#top{width: 100%; min-width:320px; height: 80px;  background-color: #1b2a7b; margin-bottom: 30px; z-index: 90;}
.all_menu{width:10%; height:30px;  float:left;}
.all_menu img {padding-top: 10px; padding-left: 10px; cursor: pointer;}


/* menu_wrap */
.menu_wrap{width: 200px; height: 100%; background-color: rgba(27, 42, 123, 0.9); position: fixed; left: -50%; top: 0; transition: all 0.5s ease-in; z-index: 80; }
.menu {width: 100%; height: 400px; margin-top: 30px; position: absolute; left: 50%; top: 50%; transform: translate(-50%,-50%);}
.menu li{ margin-bottom: 20px; text-align: center;}
.menu li a{color: #fff; }
.menu_close{font-size: 20px; color: #fff; font-weight: bold; position: absolute; right:10px; top: 10px; cursor: pointer;}

.toggle{left: 0;}


/* 제목 */
.top_fix{width: 100%; min-width:320px; height: 85px;  background-color: #1b2a7b; position: fixed; left: 0;top: 0; z-index:50; margin-bottom: 90px; }
.top_fix h1{ width: 80%; height:40px; font-weight: bold; text-align: center; font-size: 32px; float: left; line-height: 40px; padding-top: 5px; }
/* #top > h1 > img{ width: 40px; height: 40px;  border: 1px solid #000; padding-top: 10px;} */
.top_fix h1 a{color: #fff; height: 40px; line-height: 40px; background: url(../public/images/logo_white.png) no-repeat left center; padding-left: 40px; background-size: 30px;}

/* 우측상단 */
.all_info img{padding-top: 10px; padding-left: 10px; cursor: pointer;}

/* 네비메뉴 */
#main_menu{width: 100%; height: 30px; border-top: 1px solid #fff; }
#main_menu ul{width: 100%; height: 30px; padding-top: 5px;}
#main_menu ul li{float: left; width: 25%; height: 30px; background-color: #1b2a7b; color: #fff;}
#main_menu ul li a {float: left; width: 100%;  height: 30px; line-height: 30px; text-align: center; color: #fff; }
#main_menu ul li:hover a{text-decoration: underline;}


